.logoSearch{
    display: flex;
    gap:0.75rem;
    align-items: center;
    width: 100%;
}
.logoSearch >img{
    width: 3rem;
    height: 3rem;
}
.search{
    display: flex;
    background-color: var(--inputColor);
    border-radius: 10px;
    padding: 5px;
    
}
.search input{
    outline: none;
    background-color: transparent;
    border: none;
    padding-right: 6rem;
}
.s-icon{
    display: flex;
    align-items: center;
    justify-content: center;
    background: linear-gradient(106.23deg,
        #f99827,
        #f95f35 100%);
    border-radius: 5px;
    padding: 4px;
    margin-right: 5px;
    color: white;
    width: 18px;
    height: 18px;
}
.s-icon :hover{
    cursor: pointer;
}
.users-search{
    position: absolute;
    z-index: 99;
    margin-top:2.6rem;
    align-self: flex-start;
    margin-left: 3.7rem;

}
.all-users{
    background:#e4ffffe0;
    width: 17rem;
    padding: 0.3rem;
    border-radius: 5px;
    margin: 4px 0px;
    color: var(--black);
}
.all-users:hover{
    background: #d8d8d8d3;;
}